.container {
    /* position: absolute; */
    width: 420px;
    height: 420px;
    perspective: 1000px;
}

.icon {
   padding:0.8rem;
   background: var(--darkblue);
    color: var(--primary-orange);
    font-size: 60px;
    border-radius: 50%;
}

.btn-rating {
    display: flex;
    justify-content: center;
    gap: 1rem;
}

.btn-rating-item {
    font-size: 16px;
    padding: .8rem;
    border-radius: 50%;

    flex: 1;
    font-weight: 400;
    color: var(--primary-white);
    background: var(--darkblue);
    transition: var(--transition);
}

.btn-rating-item:hover {
    background: var(--primary-orange);
    color: var(--primary-white);
}

.active {
    background: var(--hover-grey);
    color: var(--primary-white);
}

.btn-submit {
    display: block;
    border-radius: 2rem;
    padding: 0.8rem;
   font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    background: var(--primary-orange);
    color: var(--primary-white);
    transition: var(--transition);
}

.btn-submit:hover {
    background: var(--primary-white);
    color: var(--primary-orange);
}

.indexMsg {
    line-height: 24px;
    height: 24px;
    text-align: center;
    vertical-align: middle; 
    margin-bottom: 0.5rem;
  }


.indexMsg small {
    color: var(--primary-orange);
    background: var(--darkblue);
    padding:0.5rem 1rem;
    border-radius: 1rem;
}

.errorMsg {
    color: red;
}

.flip-card {
    position: relative;
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    transition: var(--transition);
}

.flip-card-container .flip-card {
    transform: rotateX(180deg);
}

.flip-card-front,
.flip-card-back {

    border-radius: 2rem;
    width: 90%;
    height: 90%;
    padding: 2rem;
    display: flex;
    gap: 1.5rem;
    flex-direction: column;
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    /* Safari */
    backface-visibility: hidden;
    background: var(--primary-background-grey);
}
.flip-card-back{
    align-items: center;
    text-align: center;
}

.flip-card-back {
    transform: rotateX(180deg);
}

.icon-large {
    color:var(--primary-orange);
    font-size: 150px;
    margin: auto 0;
}

@media screen and (max-width:600px) {
    .container {
        /* position: absolute; */
        width: 380px;
        height: 380px;
        perspective: 1000px;
    }
}

