@import url('https://fonts.googleapis.com/css2?family=Overpass&family=Poppins:wght@600&display=swap');

* {
  margin: 0;
  font-family: 'Overpass', sans-serif;
  border: none;
  padding: 0;
  box-sizing: border-box;
}

body {
  color: #fff;
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background: #000000;

}
p{
  font-size: 15px;
}
section {
  margin: 0 auto;
}

small {
  color: #959494;
}

:root {
  --transition: all 400ms ease;

  --primary-orange: hsl(25, 97%, 53%);
  --primary-white: hsl(0, 0%, 100%);
  --hover-grey: hsl(217, 12%, 63%);
  --secondary-background-grey: hsl(216, 12%, 54%);
  --darkblue: hsl(213, 19%, 18%);
  --primary-background-grey: hsl(216, 12%, 8%);
}